<template>
    <div class="mapPage">
        <SideBar :sites="sites"/>
        <div v-if="ready" class="pageContent">
            <GoogleMap id="fullMap" :displayPinsOnly="false" v-bind:sites="sites" v-bind:ready="ready"/>
        </div>
    </div>
</template>

<script>
import SideBar from '../components/SideBar';
import GoogleMap from '../components/GoogleMap';

export default {
    name: 'Map',
    components: {
        SideBar,
        GoogleMap
    },
    props: [
        'sites'
    ],
    data() {
        return {
            ready: false
        }
    },
    async beforeCreate() {
        let tokenVerified = false;
        await this.$store.dispatch('verifyCurrentUser')
        .then(() => {
            tokenVerified = true;
        })
        .catch((error) => {
            console.log('error: '+error);
            if (error == 3) {
                this.$router.push('/error');
                return;
            }
            this.$router.push('/');
        });
        if (tokenVerified) {
            await this.$root.getTownData();
            this.ready = true;
        }
    }
}
</script>

<style>
#fullMap {
    position: absolute;
    background-color: rgba(200, 200, 50, 0.5);
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
}
</style>