<template>
    <div class="DropDown">
        <label>{{title}}</label>
        <div class="options">
            <button @click="opened = !opened">{{options[selectedOption].name}}</button>
            <ul v-if="opened">
                <li @click="chooseOption(option)" v-bind:key="option.key" v-for="option in options">{{option.name}}</li>
            </ul>
        </div>
        
    </div>
</template>

<script>
export default {
    name: 'DropDown',
    props: [
        'title',
        'dropDownKey',
        'options',
    ],
    data() {
        return {
            opened: false,
            selectedOption: 0
        }
    },
    computed: {
        value: function() {
            return this.options[this.selectedOption].value;
        }
    },
    mounted() {
        this.$emit('dropDownChanged', {
            dropDownKey: this.dropDownKey,
            value: this.value,
            initial: true
        });
    },
    methods: {
        chooseOption(option) {
            const previouslySelectedOption = this.selectedOption;
            for (let i = 0; i < this.options.length; i++) {
                if (this.options[i].name == option.name) {
                    this.selectedOption = i;
                }
            }
            this.opened = false;
            if (previouslySelectedOption != this.selectedOption) {
                this.$emit('dropDownChanged', {
                    dropDownKey: this.dropDownKey,
                    value: this.value,
                    initial: false
                });
            }
        }
    }
}
</script>

<style scoped>
* {
    text-align: left;
}

.options, label {
    position: relative;
    display: inline;
}

ul {
    position: absolute;
    left: 0;
    top: 2px;
    width: max-content;
    background-color: white;
    border: 1px solid black;
    border-radius: 5px;
    list-style-type: none;
    padding: 0;
    overflow: hidden;
}

li {
    padding: 0.5em 0.75em 0.5em;
    /* width: max-content; */
}

li:hover {
    background-color: rgb(44, 184, 44);
    color: white;
    cursor: pointer;
}
</style>