<template>
    <aside class="SideBar">
        <div @click="logOut()" class="logout">
            <img :src="logoutIcon" alt>
            <span>odhlásit se</span>
        </div>
        <NavBar/>
        <h2>stanoviště:</h2>
        <div class="sitesList">
            <SiteCard v-bind:key="site.id" v-for="site in sites" v-bind:site="site"/>
            <div class="credits">
                <div>Icons made by <a href="https://www.freepik.com" title="Freepik">Freepik</a> and <a href="https://www.flaticon.com/authors/gregor-cresnar" title="Gregor Cresnar">Gregor Cresnar</a> from <a href="https://www.flaticon.com/" title="Flaticon">www.flaticon.com</a></div>
            </div>
        </div>
    </aside>
</template>

<script>
import SiteCard from './SiteCard';
import NavBar from './NavBar';
import logoutIcon from '../assets/icons/logout.png';

export default {
    name: 'SideBar',
    components: {
        SiteCard,
        NavBar
    },
    props: [
        'sites'
    ],
    data() {
        return {
            logoutIcon: logoutIcon
        }
    },
    methods: {
        logOut() {
            this.$store.dispatch('logOut');
            this.$router.push('/');
        }
    }
}
</script>

<style>
.SideBar {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    width: 20em;
    background-color: white;
    box-shadow: 1px 0 18px rgb(0, 0, 0);
    z-index: 1;
}
.sitesList {
    position: absolute;
    top: 9.2em;
    bottom: 0;
    width: 100%;
    overflow-x: hidden;
    overflow-y: auto;
    display: flex;
    flex-direction: column;
}
.logout {
    width: fit-content;
    text-align: left;
    display: flex;
    align-items: center;
    opacity: 0.7;
    padding-top: 0.2em;
    padding-left: 0.4em;
    padding-right: 1em;

    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    transition: 0.35s;
}
.logout:hover {
    opacity: 1;
    cursor: pointer;
    padding-left: 0.3em;
    padding-right: 1.1em;
}
.logout img {
    width: 1.5em;
    height: 1.5em;
}
.logout span {
    font-size: 1.2em;
    position: relative;
    left: 0.5em;
}
.credits {
    justify-self: end;
    flex-grow: 1;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    width: 100%;
    order: 999;
}
.credits div {
    background-color: rgb(247, 247, 247);
    padding: 0.5em;
}
</style>