<template>
    <div class="ProgressBar">
        <div class="bar">
            <div v-bind:style="'width: ' + fillPercentage*100 + '%; background-color: ' + getFillamentColor()" class="fillament"></div>
        </div>
    </div>
</template>

<script>
import config from '../support/config.json';

export default {
    name: "ProgressBar",
    props: [
        "fillPercentage"
    ],
    methods: {
        getFillamentColor() {
            if (this.fillPercentage >= config.fill.veryFull) {
                return 'red';
            } else if (this.fillPercentage >= config.fill.prettyFull) {
                return 'orange';
            } else {
                return 'limegreen';
            }
        }
    }
}
</script>

<style>
.bar {
    height: 100%;
    margin: 0 0.5em;
    border: 1px solid rgb(128, 128, 128);
    border-radius: 0.4em;
    background-color: rgb(240, 240, 240);
    overflow: hidden;
}

.fillament {
    background-color: yellow;
    box-shadow: 1px 0 black;
    height: 100%;
}
</style>