import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';

import config from './support/config.json';

Vue.use(Vuex)

const getDefaultState = () => { return {
    loginErrorCode: 0,
    user: {
        token: null,
        isAdmin: false
    },
    currentSiteView : 1,
    selectedSite: 1
}};

export const store = new Vuex.Store({
    state: {
        loginErrorCode: 0,
        user: {
            token: null,
            isAdmin: false
        },
        currentSiteView : 1,
        selectedSite: 1
    },
    mutations: {
        setUser(state, payload) {
            state.user.token = payload.token;
            state.user.isAdmin = payload.isAdmin;
        },
        setLoginErrorCode(state, payload) {
            state.loginErrorCode = payload;
        },
        setDefaultState(state) {
            Object.assign(state, getDefaultState());
        },
        setCurrentSiteView(state, payload) {
            console.log('before ',state.currentSiteView);
            state.currentSiteView = payload;
            console.log('after ',state.currentSiteView);
        },
        setSelectedSite(state, payload) {
            state.selectedSite = payload;
        }
    },
    actions: {
        logIn(store, userData) {
            userData.isAdmin = userData.isAdmin ? true : false;
            localStorage.token = userData.token;
            localStorage.isAdmin = userData.isAdmin;
            store.commit('setUser', userData);
        },
        logOut(store) {
            const token = store.state.user.token;
            localStorage.removeItem('token');
            localStorage.removeItem('isAdmin');
            store.commit('setUser', getDefaultState().user);
            console.log('logging out for token ' + token);
            axios.post(config.backendHost+'/logout', {token: token});
        },
        async verifyCurrentUser(store, verifyAdmin = false) {
            store.commit('setLoginErrorCode', 0);
            let token = store.state.user.token;
            let isAdmin = store.state.user.isAdmin == 'true';
            if (token == null) {
                if (localStorage.token) {
                    token = localStorage.token;
                    isAdmin = localStorage.isAdmin == 'true';
                    store.commit('setUser', {token: token, isAdmin: isAdmin});
                } else {
                    return new Promise((resolve, reject) => {
                        store.commit('setLoginErrorCode', 1);
                        reject(1);
                    });
                }
            }

            return new Promise((resolve, reject) => {
                axios.post(config.backendHost+'/verifyToken', {token: token, isAdmin: isAdmin})
                .then((response) => {
                    if (verifyAdmin && !response.data.isAdmin) {
                        localStorage.isAdmin = false;
                        reject(4);
                    }
                    resolve(response.data);
                })
                .catch((error) => {
                    console.log(error);
                    console.log(error.response);
                    if (error.response) {
                        if (error.response.status == 403) {
                            store.commit('setLoginErrorCode', 2);
                        }
                        store.dispatch('logOut');
                        reject(2);
                    } else {
                        store.commit('setLoginErrorCode', 3);
                        reject(3);
                    }
                });
            });
        }
    },
    modules: {
    }
})