<template>
<div class="Login">
    <h1>Kontejměr Jičín</h1>
    <form @submit.prevent="login()">
        <input v-model="username" type="text" placeholder="username"><br>
        <input v-model="password" type="password" placeholder="password"><br><br>
        <button>přihlásit</button>
    </form>
</div>
</template>

<script>
import axios from 'axios';

import config from '../support/config.json';

export default {
    name: 'Login',
    data() {
        return {
            username: '',
            password: ''
        }
    },
    async beforeCreate() {
        if (localStorage.token) {
            await this.$store.dispatch('verifyCurrentUser')
            .then(() => {
                this.$router.push('/map');
            })
            .catch(() => {
                this.displayLoginErrorMessage();
            });
        }
    },
    mounted() {
        this.displayLoginErrorMessage();
    },
    methods: {
        async login() {
            await axios.post(config.backendHost+'/login', {
                'username': this.username,
                'password': this.password
            })
            .then(result => {
                if (result.status == 200) {
                    console.log('success');

                    //log in the user
                    this.$store.dispatch('logIn', result.data);
                    this.$router.push('/map');
                }
            })
            .catch(error => {
                if (error.response) {
                    console.log('http status: ' + error.response.status);
                    if (error.response.status == 403) {
                        alert ('nesprávné přihlašovací údaje');
                    }
                } else if (error.request) {
                    alert ('server neodpovídá');
                } else {
                    console.log('nepodarilo se odeslat zadost o prihlaseni');
                }
            });

            // if (!token) return;

            // await axios.post('http://localhost:3001/getTownData', {token: token})
            // .then(result => {
            //     console.log(result);
            // });

            
        },
        displayLoginErrorMessage() {
            const errorCode = this.$store.state.loginErrorCode;
            //0 == no error
            if (errorCode != 0) {

                for (let i = 0; i < config.loginErrorMessages.length; i++) {
                    if (errorCode == config.loginErrorMessages[i].code) {
                        alert (config.loginErrorMessages[i].message);
                    }
                }
            }
            this.$store.commit('setLoginErrorCode', 0);
        }
    }
}
</script>

<style scoped>
.Login {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    display: flex;
    flex-flow: column;
    align-items: center;
    background-color: white;
}

h1 {
    margin-top: 1.75em;
    font-size: 3em;
    color: rgb(44, 184, 44);
    text-shadow: 0px 1px 1px black;
}

form {
    margin-top: 2em;
    width: fit-content;
    height: fit-content;
    /* border: 2px solid rgb(200, 200, 200); */
    border: 0.5em solid rgb(241, 241, 241);
    border-radius: 10px;
    padding-bottom: 1em;
    background-color: rgb(241, 241, 241);
}

input {
    height: 2em;
    margin-top: 1em;
    margin-left: 1em;
    margin-right: 1em;
    border: 2px solid rgb(241, 241, 241);
    border-radius: 5px;
    padding: 0.2em;
    padding-left: 0.5em;
}
input:focus {
    outline: none;
    border: 2px solid rgb(44, 184, 44);
}

button {
    font-size: 1.2em;
    padding: 0.5em;
    color: gray;
    background-color: white;
    border: 2px solid rgb(216, 216, 216);
    border-radius: 5px;
    transition: 0.3s;
}
button:focus {
    outline: none;
    border: 2px solid rgb(44, 184, 44);
    background-color: rgb(44, 184, 44);
    color: white;
}
button:hover {
    cursor: pointer;
    border: 2px solid rgb(44, 184, 44);
    background-color: rgb(44, 184, 44);
    color: white;
}
</style>