<template>
<div id="Administration">
    <SideBar :sites="sites"/>
    <div class="pageContent">
        <h1>administrace</h1>
        <div class="card">
            <h2>přejmenovat stanoviště</h2>
            <div class="siteToRename" v-bind:key="site.id" v-for="site in sites">
                <h3>stanoviště {{site.id}}&nbsp;&nbsp;-&nbsp;&nbsp;"{{site.name}}"</h3>
                <div class="rename">
                    <span>přejmenovat na: </span><input :id="'newName_'+site.id" type="text" placeholder="nepřejmenovávat">
                </div>
            </div>
        </div>
        <button @click="applyChanges">odeslat změny</button>
    </div>
</div>
</template>

<script>
import axios from 'axios';
import SideBar from '../components/SideBar';
import config from '../support/config.json';

export default {
    name: 'Administration',
    props: [
        'sites',
        'wasteTypes'
    ],
    components: {
        SideBar
    },
    methods: {
        async applyChanges() {
            for (let i = 0; i < this.sites.length; i++) {
                const node = document.getElementById('newName_'+this.sites[i].id);
                const newName = node.value;
                const oldName = this.sites[i].name;
                const siteId = this.sites[i].id;
                
                if (newName == '') {
                    continue;
                }
                
                await axios.post(config.backendHost+'/renameSite', {
                    token: this.$store.state.user.token,
                    siteId: siteId,
                    newName: newName
                })
                .then((result) => {
                    console.log(result);
                    if (result.status == 200) {
                        alert('stanoviště '+oldName+' úspěšně přejmenováno na '+newName);
                    }
                }).catch((error) => {
                    if (error.response) {
                        console.log('http status: ' + error.response.status);
                        if (error.response.status == 403) {
                            alert('nejste admin, nemůžete přejmenovat stanoviště!');
                        } else if (error.response.status == 500) {
                            alert('někde se stala chyba, stanoviště '+oldName+' se nepodařilo přejmenovat');
                        }
                    } else if (error.request) {
                        alert('server neodpovídá');
                    } else {
                        alert('nepodařilo se odeslat žádost o přejmenování stanoviště '+oldName);
                    }
                });
            }

            this.$router.go();
        }
    },
    async beforeCreate() {
        let tokenVerified = false;
        await this.$store.dispatch('verifyCurrentUser', true)
        .then(() => {
            tokenVerified = true;
        })
        .catch((error) => {
            console.log('error: '+error);
            if (error == 3) {
                this.$router.push('/error');
                return;
            }
            if (error == 4) {
                this.$router.push('/');
                let errorMsg;
                for (let i = 0; i < config.loginErrorMessages.length; i++) {
                    if (config.loginErrorMessages[i].code == 4) {
                        errorMsg = config.loginErrorMessages[i].message;
                    }
                }
                alert(errorMsg);
                return;
            }
            this.$router.push('/');
        });
        if (tokenVerified) {
            await this.$root.getTownData();
            this.ready = true;
        }
    }
}
</script>

<style scoped>
h2 {
    font-size: 1.25em;
    height: 1em;
    padding: 0.5em;
    text-align: left;
    border: none;
    border-bottom: 2px solid rgb(70, 70, 70);
}

.siteToRename {
    position: relative;
    margin-top: 1em;
    text-align: left;
}

.card {
    background-color: white;
    box-shadow: 0px 9px 15px rgba(0, 0, 0, 0.65);
    border-radius: 1em;
    margin: 2em;
    padding: 1em;
    padding-top: 0;
}

h3 {
    font-size: 1.1em;
    margin: 0;
}

.rename {
    position: relative;
    left: 1.5em;
    margin-bottom: 1em;
}

button {
    position: relative;
    font-size: 1.2em;
    padding: 0.5em;
    background-color: white;
    border: 2px solid rgb(216, 216, 216);
    border-radius: 5px;
    transition: 0.3s;
    margin-top: 1.2em;
}
button:focus {
    outline: none;
    border: 2px solid rgb(44, 184, 44);
    background-color: rgb(44, 184, 44);
    color: white;
}
button:hover {
    cursor: pointer;
    border: 2px solid rgb(44, 184, 44);
    background-color: rgb(44, 184, 44);
    color: white;
}
</style>