<template>
    <div class="Site">
        <SideBar :sites="sites"/>
        <div v-if="ready" class="pageContent">

            <!-- {{getSiteData()}} -->
            <h1>{{site.name}}</h1>
            <div class="sectionLeft">
                <div class="scrollableSection">
                    <ContainerCard v-bind:wasteTypes="wasteTypes" v-bind:key="containerData.id" v-bind:container="containerData" v-for="containerData in site.containers"/>
                </div>
            </div>

            <div class="sectionRight">
                <GoogleMap :displayPinsOnly="true" v-bind:ready="ready" :sites="[site]"/>
            </div>

        </div>
    </div>
</template>

<script>
import SideBar from '../components/SideBar';
import ContainerCard from '../components/ContainerCard';
import GoogleMap from '../components/GoogleMap';

export default {
    name: 'Site',
    components: {
        SideBar,
        ContainerCard,
        GoogleMap
    },
    props: [
        'sessionInfo',
        'sites',
        'wasteTypes'
    ],
    data() {
        return {
            ready: false
        }
    },
    computed: {
        site: function () {
            return this.sites[this.siteId];
        },
        siteId: function () {
            const databaseId = this.$store.state.currentSiteView;
            for (let i = 0; i < this.sites.length; i++) {
                if (this.sites[i].id == databaseId) {
                    return i;
                }
            }
            return 0;
        }
    },
    methods: {
        getSiteId() {
            const queryString = window.location.search;
            const urlParams = new URLSearchParams(queryString);
            const contId = parseInt(urlParams.get('id'));
            let returnId = 0;
            for (let i = 0; i < this.sites.length; i++) {
                if (this.sites[i].id == contId) {
                    returnId = i;
                }
            }
            return returnId;
        }
    },
    async beforeCreate() {
        let tokenVerified = false;
        await this.$store.dispatch('verifyCurrentUser')
        .then(() => {
            tokenVerified = true;
        })
        .catch((error) => {
            console.log('error: '+error);
            if (error == 3) {
                this.$router.push('/error');
                return;
            }
            this.$router.push('/');
        });
        if (tokenVerified) {
            await this.$root.getTownData();
            this.ready = true;
        }
    }
}
</script>

<style>
.sectionLeft {
    position: absolute;
    top: 3.5em;
    left: 0;
    bottom: 0;
    width: 100%;
}
.scrollableSection {
    position: absolute;
    /*left: 1em;*/
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    overflow-x: hidden;
    overflow-y: auto;
    padding-top: 1.5em;
    display: flex;
    flex-flow: column;
}
.ContainerCard {
    height: fit-content;
    margin-left: 2.5em;
    margin-right: 45%;
    margin-bottom: 2em;
}
.sectionRight {
    position: absolute;
    top: 3em;
    right: 10px;
    bottom: 0;
    width: 45%;
}
.sectionRight .GoogleMap {
    position: relative;
    left: 10px;
    margin: 2em;
    min-width: 2em;
    height: stretch;
    min-height: 2em;
    /* height: 28em; */
    top: 0;
    bottom: 0;
    border-radius: 1em;
    overflow: hidden;
    box-shadow: 0px 9px 15px rgba(0, 0, 0, 0.65);
}
</style>