import Vue from 'vue';
import App from './App.vue';
import * as VueGoogleMaps from 'vue2-google-maps';

import { router } from './routes.js';
import { store } from './store.js';
import axios from 'axios';

import config from './support/config.json';


Vue.config.productionTip = false

Vue.use(VueGoogleMaps, {
  load: {
    key: 'AIzaSyBnOjDji4ADhUKLyUWoyUR7ib_BFPxDia8'
  }
});

new Vue({
  router,
  store,
  data(){ 
    return {
      firstTownDataReady: false,
      sites: [],
      wasteTypes: []
    }
  },
  methods: {
    getTownData() {
      return new Promise((resolve, reject) => {
        const token = this.$store.state.user.token;
        axios.post(config.backendHost+'/getTownData', {token: token})
        .then((result) => {
          if (result.status == 200) {
            this.sites = result.data.sites;
            this.wasteTypes = result.data.wasteTypes;
            this.firstTownDataReady = true;
            resolve();
          }
        })
        .catch((error) => {
          this.$router.push('/error');
          reject(error);
        })
      });
      
    }
  },
  render: h => h(App),
}).$mount('#app');
