<template>
<div :style="'order: ' + order" class="ContainerCard">
    <div :style="'background-color: ' + cardBackgroundColor" class="card">
        <h3>{{getWasteTypeName()}}</h3>
        <div class="warningMessagesList">
            <WarningMessage :message="message" v-bind:key="message.key" v-for="message in warningMessages"/>
        </div>
        <div class="cardBottomSection">
            <canvas :id="'container_'+container.id" class="graph"></canvas>
            <div class="rightSection">
                <div>
                    <span>{{Math.round(container.fill * 100) + '%'}}</span>
                </div>
                <img :src="baterryIcon">
            </div>
        </div>
        
    </div>
</div>
</template>

<script>
import Chart from 'chart.js/auto';
import getRecordsByDays from '../scripts/getRecordsByDays.js';
import config from '../support/config.json';

import WarningMessage from './WarningMessage';

import baterry_green from '../assets/icons/baterry_green.png';
import baterry_orange from '../assets/icons/baterry_orange.png';
import baterry_red from '../assets/icons/baterry_red.png';

export default {
    name: 'ContainerCard',
    components: {
        WarningMessage
    },
    props: [
        'container',
        'wasteTypes'
    ],
    data() {
        return {
            containerHistory: [
                {   date: '1. 1.',
                    fill: 0.20 },
                {   date: '2. 1.',
                    fill: 0.25 },
                {   date: '3. 1.',
                    fill: 0.28 },
                {   date: '4. 1.',
                    fill: 0.30 },
                {   date: '5. 1.',
                    fill: 0.40 },
                {   date: '6. 1.',
                    fill: 0.50 },
                {   date: '7. 1.',
                    fill: 0.55 },
            ]
        }
    },
    computed: {
        baterryIcon: function() {
            const baterry = this.container.baterry;
            return (
            baterry > 0.5 ?
                baterry_green :
            baterry > 0.25 ?
                baterry_orange :
                baterry_red
            );
        },
        order: function() {
            if (this.isBlocked || this.isDead || this.isOnLowBaterry) return 0;
            if (this.isVeryFull) return 1;
            if (this.isPrettyFull) return 2;
            return 3;
        },
        cardBackgroundColor: function() {
            return (
                this.isBlocked ||
                this.isOnLowBaterry ||
                this.isDead
            ) ? 'rgb(255, 165, 170);' : 'white' ;
        },
        warningMessages: function() {
            let messages = [];
            if (this.isBlocked) messages.push('kontejner je zablokovaný');
            if (this.isOnLowBaterry) messages.push('kontejner potřebuje dobít baterii');
            if (this.isDead) messages.push('kontejner neodpovídá už ' + Math.round(this.container.daysSinceUpdated) + ' dní');
            return messages;
        },

        isVeryFull: function() {
            return this.container.fill >= config.fill.veryFull;
        },
        isPrettyFull: function() {
            return this.container.fill >= config.fill.prettyFull;
        },
        isBlocked: function() {
            return this.container.blocked;
        },
        isOnLowBaterry: function() {
            return this.container.baterry < config.baterry.orange;
        },
        isDead: function() {
            return this.container.daysSinceUpdated >= config.daysToConsiderAContainerDead;
        }
    },
    created() {
        const daysOnGraph = config.daysToDisplayOnContainerGraph;
        getRecordsByDays(this.container.id, daysOnGraph+2)
            .then(responseJson => {
                if (responseJson) {
                    this.containerHistory = responseJson;

                    //format 7 -> 0 days to 0 -> 1
                    for (let i = 0; i < this.containerHistory.length; i++) {
                        const daysPassed = this.containerHistory[i].difference_days;
                        this.containerHistory[i].difference_days = (-daysPassed + daysOnGraph) / daysOnGraph;
                    }

                    this.drawGraph();
                } else {
                    //api error
                    console.log(responseJson)
                }
            })
            .catch(err => {
                //connection error
                console.log(err);
            });
    },
    methods: {
        getWasteTypeName() {
            for (let i = 0; i < this.wasteTypes.length; i++) {
                if (this.container.type_id == this.wasteTypes[i].id) {
                    return this.wasteTypes[i].name;
                }
            }
            return 'defuault waste type';
        },
        drawGraph() {
            const graphId = 'container_' + this.container.id;            
            const ctx = document.getElementById(graphId);

            let labels = [];
            let values = [];
            this.containerHistory.forEach(record => {
                labels.push(record.record_datetime_formated);
                values.push({
                    x: record.difference_days,
                    y: record.fill
                });
            });

            let graphColor;
            let graphPointsColor = null;
            graphColor = graphPointsColor;
            switch (this.container.type) {
                case 'paper':
                    graphColor = 'rgba(0, 0, 255, 0.19)';
                    graphColor = 'rgba(0, 0, 255, 0.4)';
                    graphPointsColor = 'rgba(0, 0, 255, 1)';
                    break;
                case 'plastic':
                    graphColor = 'rgba(255, 255, 0, 0.4)';
                    graphColor = 'rgba(255, 255, 0, 0.5)';
                    graphPointsColor = 'rgba(255, 200, 0, 1)';
                    break;
                case 'glass':
                    graphColor = 'rgba(0, 255, 0, 0.21)';
                    graphColor = 'rgba(0, 255, 0, 0.4)';
                    graphPointsColor = 'rgba(0, 200, 0, 1)';
                    break;
                default:
                    graphColor = 'rgba(100, 100, 100, 0.25)';
                    graphColor = 'rgba(100, 100, 100, 0.5)';
                    graphPointsColor = 'rgba(100, 100, 100, 0.7)';
                    break;
            }


            new Chart(ctx, {
                type: 'line',
                data: {
                    datasets: [{
                        data: values,
                        backgroundColor: graphColor,
                        borderColor: graphPointsColor,
                        borderWidth: 3,
                        fill: true
                    }],
                    labels: labels,
                },
                options: {
                    maintainAspectRatio: false,
                    responsive: true,
                    layout: {
                        padding: {
                            right: 6,
                        }
                    },
                    plugins: {
                        legend: {
                            display: false
                        },
                        tooltip: {
                            displayColors: false,
                            callbacks: {
                                title: tooltipItem => {
                                    return labels[tooltipItem[0].dataIndex];
                                },
                                label: tooltipItem => {
                                    return Math.round(tooltipItem.raw.y * 100) + ' %';
                                }
                            }
                        }
                    },
                    scales: {
                        y: {
                            display: false,
                            min: 0,
                            max: 1,
                            beginAtZero: true,
                        },
                        x: {
                            type: 'linear',
                            display: false,
                            min: 0,
                            max: 1,
                            beginAtZero: true,
                        },
                    }
                    
                    
                }
            });
        }
    }
}
</script>

<style>
.ContainerCard .card {
    width: 100%;
    /* height: 17.6em; */
    /* height: 19em; */
    background-color: white;
    box-shadow: 0px 9px 15px rgba(0, 0, 0, 0.65);
    border-radius: 1em;
    overflow: hidden;
}
.ContainerCard .card .cardBottomSection {
    display: flex;
    height: 15em;
}
.ContainerCard .card .cardBottomSection .graph {
    position: relative;
    top: 7px;
    left: -7px;
    min-height: 100%;
    max-height: 100%;
    min-width: 75%;
    max-width: 75%;
}
.ContainerCard .card h3 {
    /* color: rgb(70, 70, 70); */
    border-color: rgb(70, 70, 70);
    font-size: 1.25em;
    height: 1em;
    padding: 0.5em;
    text-align: left;
    border: none;
}
.ContainerCard .card .warningMessagesList {
    margin-left: 0.6em;
    margin-right: 0.6em;
    border-bottom: 2px solid black;
    height: fit-content;
}
.ContainerCard .card .cardBottomSection .rightSection {
    flex-grow: 1;
    margin-left: -6px;
    display: flex;
    flex-flow: column;
    align-items: center;
    justify-content: space-evenly;
}
.ContainerCard .card .cardBottomSection .rightSection div {
    flex-grow: 1;
    font-size: 150%;
    width: 100%;
    margin-top: 1em;
    display: flex;
    align-items: center;
    justify-content: center;
}
.ContainerCard .card .cardBottomSection .rightSection img {
    width: 4em;
    margin-bottom: 3em;
    margin-top: 2em;
    object-fit: scale-down;
}
.ContainerCard .card .cardBottomSection .rightSection div span {
    /* color: rgb(70, 70, 70); */
    /* text-shadow: 0px 0px 1px rgb(70, 70, 70); */
    text-align: center;
    font-size: 2em;
}
@media screen
and (max-width : 1250px) {
    .ContainerCard .card .cardBottomSection .graph {
        min-width: 70%;
        max-width: 70%;
    }
}
@media screen
and (max-width : 1000px) {
    .ContainerCard .card .cardBottomSection .rightSection img {
        width: 2.5em;
    }
    .ContainerCard .card .cardBottomSection .rightSection div span {
        font-size: 1em;
    }
}
</style>