<template>
    <div class="statsPage">
        <div class="pageContent">
            <h1>statistiky</h1>
            <SideBar v-bind:sites="sites"/>
            <div class="card">
                <h2>průměrná doba naplnění kontejneru</h2>
                <DropDownMenu v-if="ready" v-on:dropDownChanged="updateDropDownChosenValues($event)" :dropDownKey="'avgFillTime_site'" :title="'zahrnout stanoviště: '" :options="siteDropDownOptions"/>
                <DropDownMenu v-on:dropDownChanged="updateDropDownChosenValues($event)" :dropDownKey="'avgFillTime_time'" :title="'za: '" :options="timeDropDownOptions"/>
                <br>
                <canvas id="fillTimeGraph"></canvas>
            </div>
        </div>
    </div>
</template>

<script>
import Chart from 'chart.js/auto';
import axios from 'axios';

import SideBar from '../components/SideBar';
import DropDownMenu from '../components/DropDown';

import config from '../support/config.json';

export default {
    name: 'Stats',
    components: {
        SideBar,
        DropDownMenu
    },
    props: [
        'sessionInfo',
        'sites'
    ],
    data() {
        return {
            ready: false,
            timeDropDownOptions: [
                {
                    name: 'poslední rok',
                    value: 365
                },
                {
                    name: 'poslední 3 měsíce',
                    value: 90
                },
                {
                    name: 'poslední měsíc',
                    value: 30
                },
                {
                    name: 'celá doba',
                    value: 0
                }
            ],
            dropDowns: [
                {
                    key: 'avgFillTime_site',
                    value: null,
                    ready: false
                },
                {
                    key: 'avgFillTime_time',
                    value: null,
                    ready: false
                }
            ],
            fillTimeGraph: null,
            fillTimeGraphData: []
        }
    },
    computed: {
        allDropDownsReady: function() {
            for (let i = 0; i < this.dropDowns.length; i++) {
                if (!this.dropDowns[i].ready) return false;
            }
            return true;
        },
        wasteTypes: function() {
            return this.$root.wasteTypes.map(wasteType => {
                let backgroundColor;
                let borderColor;
                switch (wasteType.type) {
                    case 'plastic':
                        backgroundColor = 'rgba(255, 200, 0, 0.4)';
                        borderColor = 'rgba(255, 200, 0, 1)';
                        break;
                    case 'paper':
                        backgroundColor = 'rgba(0, 0, 255, 0.4)';
                        borderColor = 'rgba(0, 0, 255, 1)';
                        break;
                    case 'glass':
                        backgroundColor = 'rgba(0, 255, 0, 0.4)';
                        borderColor = 'rgba(0, 200, 0, 1)';
                        break;
                }
                return {
                    id: wasteType.id,
                    name: wasteType.name,
                    backgroundColor: backgroundColor,
                    borderColor: borderColor
                };
            });
        },
        siteDropDownOptions: function() {
            let options = [];
            options[0] = {
                name: 'všechna stanoviště',
                value: []
            };
            for (let i = 0; i < this.$root.sites.length; i++) {
                const name = this.$root.sites[i].name;
                const id = this.$root.sites[i].id;
                options.push({
                    name: name,
                    value: id
                });
                options[0].value.push(id);
            }
            return options;
        },
        dropDownValues: function() {
            let values = {};
            for (let i = 0; i < this.dropDowns.length; i++) {
                values[this.dropDowns[i].key] = this.dropDowns[i].value;
            }
            return values;
        }
    },
    async beforeCreate() {
        let tokenVerified = false;
        await this.$store.dispatch('verifyCurrentUser')
        .then(() => {
            tokenVerified = true;
        })
        .catch((error) => {
            console.log('error: '+error);
            if (error == 3) {
                this.$router.push('/error');
                return;
            }
            this.$router.push('/');
        });
        if (tokenVerified) {
            await this.$root.getTownData();
            this.ready = true;
            // this.graphDrewAlready = true;
            // this.drawFillTimeGraph();
        }
    },
    mounted() {
    //    if (!this.graphDrewAlready && this.ready) this.drawFillTimeGraph();
    },
    methods: {
        updateDropDownChosenValues(dropDownData) {
            let index = null;
            for (let i = 0; i < this.dropDowns.length; i++) {
                if (this.dropDowns[i].key == dropDownData.dropDownKey) {
                    index = i;
                    break;
                }
            }
            if (index === null) return;


            const value = dropDownData.value;
            this.dropDowns[index].value = value;
            this.dropDowns[index].ready = true;
            
            if (this.allDropDownsReady && this.ready) {
                axios.post(config.backendHost + '/averageFillTimePerType', {
                    token: localStorage.token,
                    sites: this.dropDownValues.avgFillTime_site,
                    time: this.dropDownValues.avgFillTime_time,
                })
                .then((response) => {
                    this.fillTimeGraphData = response.data;
                    this.drawFillTimeGraph();
                })
                .catch((error) => {
                    console.log(error);
                })
            }
        },
        drawFillTimeGraph() {
            const ctx = document.getElementById('fillTimeGraph').getContext('2d');

            let wasteTypeAverages = [];
            let wasteTypeNames = [];
            for (let i = 0; i < this.wasteTypes.length; i++) {
                wasteTypeNames.push(this.wasteTypes[i].name);
                for (let j = 0; j < this.fillTimeGraphData.length; j++) {
                    if (this.fillTimeGraphData[j].typeId == this.wasteTypes[i].id) {
                        wasteTypeAverages.push(Math.round(this.fillTimeGraphData[j].daysToFill*10)/10);
                        break;
                    }
                }
            }
            // const wasteTypeNames = this.wasteTypes.map(type => {
            //     return type.name
            // });
            const backgroundColors = this.wasteTypes.map(type => {return type.backgroundColor});
            const borderColors = this.wasteTypes.map(type => {return type.borderColor});

            if (this.fillTimeGraph !== null) {
                this.fillTimeGraph.destroy();
            }

            this.fillTimeGraph = new Chart(ctx, {
                type: 'bar',
                data: {
                    labels: wasteTypeNames,
                    datasets: [{
                        data: wasteTypeAverages,
                        backgroundColor: backgroundColors,
                        borderColor: borderColors,
                        borderWidth: 3
                    }]
                },
                options: {
                    maintainAspectRatio: false,
                    responsive: true,
                    title: {
                        display: false
                    },
                    plugins:{legend: {
                        display: false
                    }},
                    scales: {
                        y: {
                            title: {
                                display: true,
                                text: 'dny',
                                font: {
                                    size: 17
                                }
                            },
                            ticks: {
                                suggestedMin: 0
                            }
                        },
                        x: {
                            title: {
                                display: true,
                                text: 'typ odpadu',
                                font: {
                                    size: 17
                                }
                            },
                            grid: {
                                display: false
                            }
                        }
                    }
                }
            });

            this.graphDrewAlready = true;
        }
    }
}
</script>

<style scoped>
h2 {
    font-size: 1.25em;
    height: 1em;
    padding: 0.5em;
    text-align: left;
    border: none;
    border-bottom: 2px solid rgb(70, 70, 70);
}

.card {
    background-color: white;
    box-shadow: 0px 9px 15px rgba(0, 0, 0, 0.65);
    border-radius: 1em;
    margin: 2em;
    padding: 1em;
    padding-top: 0;
}

#fillTimeGraph {
    max-height: 16.75em;
}

h3 {
    font-size: 1.1em;
    margin: 0;
}
</style>