<template>
    <router-link :style="'order: ' + order" class="SiteCard" to="site">
        <div :style="'background-color: ' + cardBackgroundColor" @click="setCurrentSiteView" class="card">
            <h3>{{site.name}}</h3>
            <div class="warningMessagesList">
                <WarningMessage :message="message" v-bind:key="message.key" v-for="message in warningMessages"/>
            </div>
            <div class="containerList">
                <!-- <WarningMessage :message="message" v-bind:key="message.key" v-for="message in warningMessages"/> -->
                <SiteCardContainerStatus v-bind:containerData="containerData" v-bind:key="containerData.id" v-for="containerData in site.containers"/>
            </div>
        </div>
    </router-link>
</template>

<script>
import SiteCardContainerStatus from './SiteCardContainerStatus';
import WarningMessage from './WarningMessage';
import config from '../support/config.json';

export default {
    name: 'SiteCard',
    components: {
        SiteCardContainerStatus,
        WarningMessage
    },
    props: [
        'site'
    ],
    methods: {
        setCurrentSiteView() {
            this.$store.commit('setCurrentSiteView', this.site.id);
        }
    },
    computed: {
        order: function() {
            if (this.blockedContainers.length > 0 || this.deadContainers.length > 0) return 0;
            if (this.hasAnyRedContainers) return 1;
            if (this.hasOrangeBaterry) return 2;
            if (this.hasAnyOrangeContainers) return 3;
            return 4;
        },
        cardBackgroundColor: function() {
            return (
                this.blockedContainers.length > 0 ||
                this.deadContainers.length > 0 ||
                this.lowBaterryContainers.length > 0
            ) ? 'rgb(255, 165, 170);' : 'white' ;
        },
        warningMessages: function() {
            let messages = [];

            //blocked containers
            for (let i = 0; i < this.blockedContainers.length; i++) {
                messages.push('kontejner na '+ this.site.containers[this.blockedContainers[i]].typeName +' je zablokovaný');
            }

            //critically low baterry containers
            for (let i = 0; i < this.lowBaterryContainers.length; i++) {
                messages.push('kontejner na '+ this.site.containers[this.lowBaterryContainers[i]].typeName +' potřebuje dobít baterii');
            }

            //dead (not sending any data) containers
            for (let i = 0; i < this.deadContainers.length; i++) {
                const container = this.site.containers[this.deadContainers[i]];
                messages.push('kontejner na '+ container.typeName +' neodpovídá už ' + Math.round(container.daysSinceUpdated) + ' dní');
            }

            return messages;
        },

        hasAnyRedContainers: function() {
            for (let i = 0; i < this.site.containers.length; i++) {
                if (this.site.containers[i].fill >= config.fill.veryFull) return true;
            }
            return false;
        },
        hasAnyOrangeContainers: function() {
            for (let i = 0; i < this.site.containers.length; i++) {
                if (this.site.containers[i].fill >= config.fill.prettyFull) return true;
            }
            return false;
        },
        hasOrangeBaterry: function() {
            for (let i = 0; i < this.site.containers.length; i++) {
                if (this.site.containers[i].baterry < config.baterry.green) {
                    return true;
                }
            }
            return false;
        },

        blockedContainers: function() {
            let blockedContainers = [];
            for (let i = 0; i < this.site.containers.length; i++) {
                if (this.site.containers[i].blocked) blockedContainers.push(i);
            }
            return blockedContainers;
        },
        lowBaterryContainers: function() {
            let lowBaterryContainers = [];
            for (let i = 0; i < this.site.containers.length; i++) {
                if (this.site.containers[i].baterry < config.baterry.orange) lowBaterryContainers.push(i);
            }
            return lowBaterryContainers;
        },
        deadContainers: function() {
            let deadContainers = [];
            for (let i = 0; i < this.site.containers.length; i++) {
                if (this.site.containers[i].daysSinceUpdated >= config.daysToConsiderAContainerDead) deadContainers.push(i);
            }
            return deadContainers;
        }
    }
}
</script>

<style>
.SiteCard {
    font-size: 1em;
    display: block;
    text-decoration: none;
    margin: 1em 5% 0 5%;
    padding: 0.5em 0 0.05em 0;
    transition: 0.125s padding linear;
}
.SiteCard .card {
    position: relative;
    top: 0;
    display: block;
    font-size: 1em;
    text-align: left;
    border-radius: 1em;
    text-decoration: none;
    transition: 0.5s;
    box-shadow: 0 5px 9px rgb(41, 41, 41);
    transition: 0.125s all linear;
    background-color: white;
}
.SiteCard .card:hover {
    /* background-color: rgb(250, 250, 255); */
    top: -0.45em;
    box-shadow: 0 5px 18px rgb(54, 54, 54);
    transition: 0.125s all linear;
}
.card h3 {
    margin: 0;
    margin-left: 0.5em;
    margin-right: 0.5em;
    padding: 0.5em;
    padding-bottom: 0.3em;
    color: black;
}
.SiteCard .card .warningMessagesList {
    margin-left: 0.55em;
    margin-right: 0.55em;
    border-bottom: 2px solid black;
    height: fit-content;
}
.containerList{
    margin: 0.5em;
    padding-bottom: 0.5em;
}
</style>