<template>
<div class="GoogleMap">
    <GmapMap v-if="ready && displayPinsOnly == false"
        :center="calculateMapCenter()"
        :zoom="14"
        map-type-id="hybrid"
    >
        <SiteInfoWindow v-bind:site="site" v-bind:key="site.key" v-for="site in sites"/>
    </GmapMap>
    <GmapMap v-if="ready && displayPinsOnly == true"
        :center="calculateMapCenter()"
        :zoom="14"
        map-type-id="hybrid"
    >
        <GmapMarker v-bind:site="site" v-bind:key="site.key" v-for="site in sites"
            :position="{lat: site.lat, lng: site.lng}"
        >
        </GmapMarker>
    </GmapMap>
</div>
</template>

<script>
import SiteInfoWindow from './SiteInfoWindow';

export default {
    name: 'GoogleMap',
    components: {
        SiteInfoWindow
    },
    props: [
        'sites',
        'displayPinsOnly',
        'ready'
    ],
    methods: {
        calculateMapCenter() {
            let totalLat = 0;
            let totalLng = 0;
            this.sites.forEach(site => {
                totalLat += site.lat;
                totalLng += site.lng;
            });
            return {
                lat: totalLat / this.sites.length,
                lng: totalLng / this.sites.length
            };
        }
    }
}
</script>

<style>
.GoogleMap {
    position: relative;
}
.vue-map-container {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
}
</style>