<template>
    <nav class="navContainer">
        <NavItem v-if="isAdmin" :link="'/admin'" :icon="icons.administration"/>
        <NavItem :link="'/stats'" :icon="icons.stats"/>
        <NavItem :link="'/map'" :icon="icons.map"/>
    </nav>
</template>

<script>
import NavItem from './NavBarItem';

//icons
import homeIcon from '../assets/icons/home.png';
import statsIcon from '../assets/icons/stats.png';
import mapIcon from '../assets/icons/map.png';
import administrationIcon from '../assets/icons/administration.png';

export default {
    name: 'NavBar',
    components: {
        NavItem
    },
    data() {
        return {
            icons: {
                home: homeIcon,
                stats: statsIcon,
                map: mapIcon,
                administration: administrationIcon
            }
        }
    },
    computed: {
        isAdmin: function () {
            return this.$store.state.user.isAdmin;
        }
    }
}
</script>

<style>
    .navContainer {
        margin: 5% 5% 0 1em;
        display: flex;
        flex-flow: row;
        justify-content: space-evenly;
    }
</style>