<template>
<GmapInfoWindow
    :options="{maxWidth: 300, maxHeight: 300}"
    :position="{lat: site.lat, lng: site.lng}"
    >
    <div :id="'iw_'+site.name" class="SiteInfoWindow" @click="setCurrentSiteView">
        <router-link to="site">
            <h3>{{site.name}}</h3>
            <div v-if="hasAnyWarnings" class="warningMessageWrapper">
                <WarningMessage style="width: 10em; left: 1px" message="stanoviětě hlásí problém"/>
            </div>
            <!-- <WarningMessage style="width: 6em; left: 2px; top: -5px;" message="stanoviětě hlásí problém"/> -->
            <div class="containerStatusWrapper">
                <SiteIWContainerStatus v-bind:container="container" v-bind:key="container.key" v-for="container in site.containers"/>
            </div>
        </router-link>
    </div>
</GmapInfoWindow>
</template>

<script>
import SiteIWContainerStatus from './SiteIWContainerStatus';
import WarningMessage from './WarningMessage';
import config from '../support/config.json';

export default {
    name: 'SiteInfoWindow',
    components: {
        SiteIWContainerStatus,
        WarningMessage
    },
    props: [
        'site'
    ],
    data() {
        return {
            timeToHideCloseButton: 4000,
            tryingToHideColseButton: true
        }
    },
    computed: {
        hasAnyWarnings: function() {
            const containers = this.site.containers;
            //any blocked containers
            for (let i = 0; i < containers.length; i++) {
                if (
                    containers[i].blocked ||
                    containers[i].baterry < config.baterry.orange ||
                    containers[i].daysSinceUpdated >= config.daysToConsiderAContainerDead
                ) return true;
            }
            return false;
        }
    },
    mounted() {
        setTimeout(() => {this.tryingToHideColseButton = false}, this.timeToHideCloseButton);
        setTimeout(this.tryToHideCloseIWButton, 0);
    },
    methods: {
        setCurrentSiteView() {
            this.$store.commit('setCurrentSiteView', this.site.id);
        },
        async tryToHideCloseIWButton() {
            let closeIWElement = await this.getElementByXpath('//div[div[div[div[@id="iw_'+this.site.name+'"][@class="SiteInfoWindow"]]]]/button').catch(() => {
                setTimeout(this.tryToHideCloseIWButton, 0);
            });
            if (closeIWElement) {
                closeIWElement.style.display = 'none';
            }
        },
        getElementByXpath(xPath) {
            let xPathResult = document.evaluate(xPath, document, null, XPathResult.UNORDERED_NODE_ITERATOR_TYPE, null).iterateNext();
            return new Promise((resolve, reject) => {
                if (!xPathResult) {
                    reject(xPathResult);
                } else {
                    resolve(xPathResult);
                }
            });
        },
        idk() {
            console.log('idk');
        }
    }
}
</script>

<style>
.SiteInfoWindow *{
    text-decoration: none;
}
.SiteInfoWindow h3 {
    color: black;
    text-align: center;
    margin-top: 0;
    margin-bottom: 0.8em;
}
.SiteInfoWindow .warningMessageWrapper {
    width: 10.5em;
    display: flex;
    align-items: center;
    justify-content: center;
}
.SiteInfoWindow .containerStatusWrapper {
    display: flex;
    flex-wrap: wrap;
    align-content: center;
    justify-content: space-evenly;
    max-width: 16em;
    padding-bottom: 0.2em;
}
</style>