<template>
    <div class="SiteCardContainerStatus">
        <img :src="wasteIcon" alt>
        <ProgressBar v-bind:fillPercentage="containerData.fill"/>
        <img v-if="containerData.baterry < 0.5" :src="baterryIcon" alt>
    </div>
</template>

<script>
import ProgressBar from './ProgressBar';

import config from '../support/config.json';

import paperIcon from '../assets/icons/waste_paper.png';
import plasticIcon from '../assets/icons/waste_plastic.png';
import glassIcon from '../assets/icons/waste_glass.png';
import baterry_green from '../assets/icons/baterry_green.png';
import baterry_orange from '../assets/icons/baterry_orange.png';
import baterry_red from '../assets/icons/baterry_red.png';

export default {
    name: 'SiteCardContainerStatus',
    components: {
        ProgressBar
    },
    props: [
        'containerData'
    ],
    data() {
        return {
            wasteIcons: {
                paper: paperIcon,
                plastic: plasticIcon,
                glass: glassIcon
            }
        }
    },
    computed: {
        wasteIcon: function() {
            return this.wasteIcons[this.containerData.type];
        },
        baterryIcon: function() {
            const baterry = this.containerData.baterry;
            if (baterry >= config.baterry.green) return baterry_green;
            if (baterry >= config.baterry.orange) return baterry_orange;
            return baterry_red;
        }
    }
}
</script>

<style>
.SiteCardContainerStatus {
    text-align: left;
    display: flex;
    align-items: center;
}
.SiteCardContainerStatus img {
    width: 1.9em;
    height: 1.9em;
    padding: 0.1em;
    object-fit: scale-down;
}
.ProgressBar {
    height: 1em;
    width: 5em;
    flex-grow: 1;
}

</style>